import React from "react";
import jf from "../../assets/img/HUAN_nevado.jpg";
import '../Start.css'
import '../../components/Institucional/HistoriaEmpresa.css';
import Interests from "../Interests";
export const HistoriaEmpresa = () => {
  return (
    <div className="start-container">
      <div className="primery-container">
        <div className="section-one">
          <div>
            <h4>INSTITUCIONAL</h4>
          </div>
          <div>
            <h5>HISTORIA DE LA EMPRESA</h5>
            <div className="container-img">
              <img src={jf} href id="nevado" />
            </div>
            <p>
              Huanuni se encuentra a una hora de la ciudad de Oruro en Bolivia ,
              el descubrimiento se remonta a la poca colonial, epoca del Virrey
              Francisco de Toledo (1745), el nombre inicial del cerro fue
              Guaricunca, que se cambi por Kcoya Orco (cerro de las minas) y
              finalmente PosoKoni como se conoce actualmente.
            </p>

            <p>
              El que habra descubierto minerales de Estao y Plata llegando a
              fundirlos sera el espaol Bernardo Cabrera el ao 1745, quien se
              asilo en el lugar denominado "guanani" que significa, he
              escarmentado o padecido, "Huauchihuankank", abria sido la
              expresion quechua por un campesino, denominativos que se generaron
              en alusin al intenso fro de la regin que poda quebrar la
              rocas. Entonces se estima que de guanani proviene el nombre de
              Huanuni.
            </p>
            <p>
              En 1840 el Sr. Tellez se adjudic varias propiedades mineras, que
              hasta el ao 1860 lleg a tener 47 propiedades; en 1883 se forma
              la compaa minera "SOCIEDAD MINERALOGICA TELLEZ HUANUNI",
              teniendo como principal colindante a Harrisn; en 1884 por fusin
              de compaas se crea la empresa "PENNY-DUNCAN", explotando la
              propiedad llamada El Polgono y los yacimiemtos ricos de Mara
              Francisca, El Porvenir, las propiedades de los cerros Posokoni,
              Viscachani y Chuallani
            </p>
            <p>
              Entre l908 y l9l0, el Industrial minero Simn Patio compra todas
              las concesiones, conformando un grupo minero que en 1931 pasa a
              integrar la PATIO MINES, delegando sus derechos a la BOLIVIAN TIN
              AND TUNGTEN CORPORATION. En el transcurso de 1949, el pas es
              convulsionado, polticamente llegando a 1952 y por Decreto de
              Nacionalizacin de la Minas, Huanuni pasa a formar parte de la
              CORPORACION MINERA DE BOLIVIA.{" "}
            </p>
            <p>
              Desde que Patio adquiri las ricas minas de la regin, a finales
              del siglo XIX, los trabajadores mineros de Huanuni ofrecieron
              resistencia a la explotacin inhumana e inmisericorde del patrn.
              En 1919, desde el 13 hasta el 17 de noviembre sostuvieron una
              heroica lucha que culmin con la conquista de las ocho horas de
              trabajo, conquista que posteriormente sera incorporado al derecho
              social boliviano. El 15 de junio de1938 se fund el Sindicato
              Mixto de Trabajadores Mineros de Huanuni. A finales de septiembre
              de 1940 se produjo un levantamiento de los mineros de Huanuni
              encabezados por la palliri Lola Irahola que tuvo repercusin
              nacional.
            </p>
            <p>
              En 1944, desde el 10 al 13 de junio se realiz en Huanuni el
              Primer Congreso de Trabajadores Mineros de Bolivia, fundndose el
              11 de Junio la poderosa Federacin Sindical de Trabajadores
              Mineros de Bolivia (FSTMB)
            </p>
            <p>
              En la dcada de los 40 se produjeron numerosos levantamientos
              sociales y entre ellos se cuenta la Masacre de Huanuni del 30 de
              mayo de 1949, donde se produjo la cada y detenciones de numerosos
              dirigentes mineros. Posteriormente, los diferentes gobiernos de
              turno repitieron las masacres una y otra vez pretendiendo acallar
              la voz de los mineros de Huanuni llegando a constituirse en la
              vanguardia del movimiento minero boliviano.
            </p>
            <p>
              1952. se da la Nacionalizacin de las Minas. Pero Patio, es
              reemplazado por un nuevo patrn, el Estado a travs de COMIBOL.
              Los parajes de explotacin siguen siendo los mismos que dejaron
              los barones del estao.
            </p>
            <p>
              1971  1978. Durante la dictadura de Banzer, boy la minera en
              Huanuni especialmente por los altos precios del estao a nivel
              internacional. En este periodo, se neutraliz a los mineros,
              especialmente de interior mina, con contratos personales que les
              permita emonumentos salariales por 50 veces ms que su salario
              bsico. No es casual que Huanuni rompi algunas huelgas de la
              FSTMB. El gobierno de entonces, en boca del ministro de mineria
              Rogelio Miranda deca con tal que se trabaje en Huanuni, el resto
              puede parar cuanto quiera
            </p>
            <p>
              En 1987, por el Decreto Supremo 21377, se descentraliza y forma
              parte de la EMPRESA MINERA SUBSIDIARIA DE ORURO como Centro Minero
              Huanuni, posteriormente por disposiciones superiores a finales de
              1,992 nuevamente toma el nombre de Empresa Minera Huanuni,
              dependiendo directamente de las Oficinas Centrales de COMIBOL;
              posteriormente el 9 de Marzo del 2000 se firma el contrato de
              riesgo comprartido con Allied Deals PLC, que posteriormente se
              llam RBG Minera Huanuni S.A. y por la quiebra de sta por fraude
              de sus principales ejecutivos la empresa es intervenida
              judicialmente a pedido del gobierno boliviano a travs de COMIBOL,
              nuevamente es modificado el nombre de la Empresa; entre Junio del
              2002 a Mayo 2006, la Empresa lleva el nombre de RBG Minera Huanuni
              S.A.-INTERVENCION, a partir del mes de Junio 2006 por disposicin
              gubernamental se resuelve el contrato de riesgo compartido y la
              Empresa es nuevamente dependiente de la CORPORACION MINERA DE
              BOLIVIA bajo el denominativo de EMPRESA MINERA HUANUNI.
            </p>
          </div>
        </div>
      </div>
      <div className="secundary-container">
        <Interests></Interests>
      </div>
    </div>
  );
};
